.nav {
    grid-area: n;
    background-color: #FDFDFD;
    padding: 20px;
}

.title {
    font-weight: 500;
    font-size: 15px;
    color: #424242;
}

.title::after {
    content: "";
    display: block;
    height: 2px;
    width: 65px;
    background-color: #088dcd;
    text-align: center;
}

.link {
    font-weight: 400;
    padding: 10px 0;
}

.link a {
    text-decoration: none;
    color: inherit;
    transition: color .2s linear;
}
.link a:hover {
    color: #088dcd;
}





